 import React from 'react';
import Navigation from './Components/Navigation/Navigation.js';
import Logo from './Components/Logo/Logo.js';
import FaceRecognition from './Components/FaceRecognition/FaceRecognition.js';
import ImageLinkForm from './Components/ImageLinkForm/ImageLinkForm.js';
import Rank from './Components/Rank/rank.js';
import Register from './Components/Register/Register.js';
import Signin from './Components/Signin/Signin.js';
import Clarifai from 'clarifai';
// import Particles from 'react-particles-js';
import './App.css';

// const particlesOption = {
// 	 particles: {
// 	 	number: {
// 	 		value: 30,
// 	 		density: {
// 	 			enable: true,
// 	 			value_area: 800
// 	 		}
// 	 	}
// 	 }
// }

const app = new Clarifai.App({
	apiKey: '69046c46e2434a7c9758db2f3c321efa'
});

class App extends React.Component
{
	constructor()
	{
		super();
		this.state={
			input: '',
			imageUrl: '',
			box: {}, //to box around fcaes
			route: 'signin',
			isSignedIn: false

		}
	}

	componentDidMount() {
		fetch('http://localhost:3001/')
		.then(response => response.json())
		.then(console.log)  //or .then(console.log)
	}

	calculateFaceLocation = (data) =>
	{
		const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
		//^this above gives percenages at which the point is wrt image..
		const image = document.getElementById('inputimage');
		const width = Number(image.width);
		const height = Number(image.height);
		console.log(width,height);
		return{	
			leftCol : clarifaiFace.left_col * width,
			topRow : clarifaiFace.top_row * height,
			rightCol: width - (clarifaiFace.right_col * width),
			bottomRow: height - (clarifaiFace.bottom_row *height)

		}
	}

	displayFaceBox = (box)=> 
	{
		console.log(box);
		this.setState({box: box});
	}


	onInputChange = (event) => 
	{
		console.log(event.target.value);
		this.setState({input: event.target.value});
	}

	onButtonClick = () => 
	{
		// console.log('click');
		
		this.setState({imageUrl: this.state.input});
		// "https://samples.clarifai.com/face-det.jpg"
		// const x = this.state.imageUrl;
		app.models.predict(Clarifai.FACE_DETECT_MODEL,this.state.input)
		.then(response => this.displayFaceBox(this.calculateFaceLocation(response)))
		.catch(err=> console.log(err));
			// {
				// console.log(response);
				// console.log(response.outputs[0].data.regions[0].region_info.bounding_box);
				
			
	// 		function(err)
	// 		{
	// 			console.log(err);
	// 		}
	// 	);
	}

	onRouteChange=(route)=>{
		if(route==='signin')
		{
			this.setState({isSignedIn: false});

		} 
		else if(route==='home') 
		{
			this.setState({isSignedIn: true});
			
		}
		this.setState({route: route});
	}

	render()
	{
		return(

			<div className="App">
			{/*}	<Particles className='particles'
			// 		params={particlesOption}
			// 	/>*/}
				<Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange} />
				{this.state.route === 'home' 
				? <div>
					<Logo/>
					<Rank/>
					<ImageLinkForm onItCe={this.onInputChange} onButtonClick={this.onButtonClick}/>
					<FaceRecognition box={this.state.box} imageUrl={this.state.imageUrl}/>
				</div>
					:(
						this.state.route === 'signin'
						? <Signin onRouteChange={this.onRouteChange}/>
						: <Register onRouteChange={this.onRouteChange}/>
				)
			}
			</div>
			);
	}
}

export default App;